
Template.__checkName("atPwdFormBtn");
Template["atPwdFormBtn"] = new Template("Template.atPwdFormBtn", (function() {
  var view = this;
  return HTML.DIV({
    "class": "padding"
  }, "\n    ", HTML.BUTTON({
    type: "submit",
    "class": function() {
      return [ "at-btn button button-positive button-block submit ", Spacebars.mustache(view.lookup("submitDisabled")) ];
    },
    id: "at-btn"
  }, "\n      ", Blaze.View("lookup:buttonText", function() {
    return Spacebars.mustache(view.lookup("buttonText"));
  }), "\n    "), "\n  ");
}));
