
Template.__checkName("atMessage");
Template["atMessage"] = new Template("Template.atMessage", (function() {
  var view = this;
  return HTML.DIV({
    "class": "at-message"
  }, "\n    ", Blaze.View("lookup:message", function() {
    return Spacebars.mustache(view.lookup("message"));
  }), "\n  ");
}));
