
Template.__checkName("atTitle");
Template["atTitle"] = new Template("Template.atTitle", (function() {
  var view = this;
  return HTML.DIV({
    "class": "item item-divider"
  }, "\n    ", HTML.H3({
    "class": "title"
  }, Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  })), "\n  ");
}));
