
Template.__checkName("atResult");
Template["atResult"] = new Template("Template.atResult", (function() {
  var view = this;
  return HTML.DIV({
    "class": "at-result"
  }, "\n    ", Blaze.View("lookup:result", function() {
    return Spacebars.mustache(view.lookup("result"));
  }), "\n  ");
}));
